import React, {useState, useEffect} from 'react';
import {db} from '../firebase';
import { fade, makeStyles } from '@material-ui/core/styles';
import Card from '@material-ui/core/Card';
import CardContent from '@material-ui/core/CardContent';
import CardMedia from '@material-ui/core/CardMedia';
import Typography from '@material-ui/core/Typography';
import Grid from '@material-ui/core/Grid';
import { CardActionArea, CardActions } from '@material-ui/core';
import InputBase from '@material-ui/core/InputBase';
import SearchIcon from '@material-ui/icons/Search';
import Container from '@material-ui/core/Container';
import Fuse from 'fuse.js';
import Comentarios from './Comentarios';
import Comentar from './Comentar';
import Loading from './Loading';
import Votar from './Votar';
// import Caritas from './Caritas';
// import Vote from './Vote';

const useStyles = makeStyles((theme) => ({
  root: {
    maxWidth: 345,
    margin: '1em',
    boxSizing: 'border-box'
  },
  media: {
    height: 240
  },
  search: {
    marginTop: '1em',
    position: 'relative',
    borderRadius: theme.shape.borderRadius,
    backgroundColor: fade(theme.palette.common.black, 0.15),
    '&:hover': {
      backgroundColor: fade(theme.palette.common.black, 0.25),
    },
    marginLeft: 0,
    width: '100%',
    [theme.breakpoints.up('sm')]: {
      marginLeft: theme.spacing(1),
      width: 'auto',
    },
  },
  searchIcon: {
    padding: theme.spacing(0, 2),
    height: '100%',
    position: 'absolute',
    pointerEvents: 'none',
    display: 'flex',
    alignItems: 'center',
    justifyContent: 'center',
  },
  inputRoot: {
    color: 'primary',
  },
  inputInput: {
    padding: theme.spacing(1, 1, 1, 0),
    // vertical padding + font size from searchIcon
    paddingLeft: `calc(1em + ${theme.spacing(4)}px)`,
    transition: theme.transitions.create('width'),
    width: '100%',
    [theme.breakpoints.up('sm')]: {
      width: '12ch',
      '&:focus': {
        width: '20ch',
      },
    },
  },
  containerCard: {
    display: 'flex',
    justifyContent: 'center'
  },
  comentar:{
    display:'flex',
    alignItems:'flex-end'
  }
}));

export default function MediaCard() {
  const classes = useStyles();
  const [veterinarias, setVeterinarias] = useState([]);
  const [query, setQuery] = useState('');

  useEffect(()=>{
    return db.collection('veterinarias')
    .onSnapshot((snapshot)=>{
      const data = [];
          snapshot.forEach((doc)=>{
            data.push(doc.data());
          })
          setVeterinarias([...data])
    })
  }, [])

console.log(veterinarias)
  const fuse = new Fuse(veterinarias, {
    keys: [
      'nombre',
      'direccion'
    ],
    includeScore: true
  })

  const resultados = fuse.search(query);
  const vetResultados = query ? resultados.map(resultado => resultado.item) : veterinarias;
  function handleOnSearch({currentTarget = {}}) {
    const {value} = currentTarget;
    setQuery(value);
  }


  const listaDatos = vetResultados.length ? vetResultados.map((veterinaria, index)=>{
    return (
        <Grid className={classes.containerCard} item xs={12} sm={6} md={4} key={index}>
            <Card className={classes.root}>
            <CardActionArea>
                <CardMedia
                className={classes.media}
                image={veterinaria.img}
                />
                <CardContent>
                <Typography gutterBottom variant="h5" component="h3">
                    {veterinaria.nombre}
                </Typography>
                <Typography variant="body2" color="textSecondary" component="p">
                    Direccion: {veterinaria.direccion}
                </Typography>
                <Typography variant="body2" color="textSecondary" component="p">
                    Horarios: {veterinaria.horario}
                </Typography>
                <Typography variant="body2" color="textSecondary" component="p">
                    Contacto: {veterinaria.telefono}
                </Typography>
                </CardContent>
                </CardActionArea>
                <CardActions className={classes.comentar}>
                  <Comentarios veterinariaKey={veterinaria.id}/>
                  <Comentar veterinariaKey={veterinaria.id}/>
                </CardActions>
                <CardActions>
                  <Votar veterinariaKey={veterinaria.id}/>
                </CardActions>
            </Card>
        </Grid> )
  })  :  <Loading/>;


  return (
  <div>
    <Container maxWidth="sm">
      <div className={classes.search}>
          <div className={classes.searchIcon}>
            <SearchIcon/>
          </div>
          <InputBase
          placeholder="Buscar…"
          classes={{
          root: classes.inputRoot,
          input: classes.inputInput,
          }}
          inputProps={{ 'aria-label': 'search' }}
          value={query}
          onChange={handleOnSearch}
          />
      </div>
    </Container>
    <Grid container spacing={3}>
        {listaDatos}
    </Grid>
  </div>
  );
}