import React from 'react';
import imagenes from '../imagenes/paw-solid.svg';
import FacebookIcon from '@material-ui/icons/Facebook';
import InstagramIcon from '@material-ui/icons/Instagram';
import TwitterIcon from '@material-ui/icons/Twitter';


function Avatar() {
    return <img src={imagenes} alt="Logo"></img>;
}

const Footer = () => {
//style={{position:'absolute', bottom:'0px', width:'100%'}} 
    return ( 
    <div className="foot" >
        <div className="logoNombre">
            <Avatar />
            <h1>Patitas Catan</h1>
        </div>
        <div className="redes">
            <FacebookIcon className="iconos"/>
            <InstagramIcon className="iconos"/>
            <TwitterIcon className="iconos"/>
        </div>
    </div> );
}

// JSX - azucar sintactico seria como una extension de js pero mucho mas optimizada
// JSX - JAVASCRIPT Y HTML/XML
export default Footer;