import React from 'react';
import AppBar from '@material-ui/core/AppBar';
import Toolbar from '@material-ui/core/Toolbar';
import IconButton from '@material-ui/core/IconButton';
import Typography from '@material-ui/core/Typography';
import { makeStyles } from '@material-ui/core/styles';
import MenuIcon from '@material-ui/icons/Menu';
import MenuItem from '@material-ui/core/MenuItem';
import Menu from '@material-ui/core/Menu';
import {authentication} from '../firebase';
import PetsIcon from '@material-ui/icons/Pets';
import AccountCircle from '@material-ui/icons/AccountCircle';


const useStyles = makeStyles((theme) => ({
  root: {
    flexGrow: 1,
  },
  menuButton: {
    marginRight: theme.spacing(2),
  },
  title: {
    flexGrow: 1,
    textAlign: 'center',
    alignItems: "center",
    display: 'flex',
    justifyContent: "center"
    // [theme.breakpoints.up('sm')]: {
    //   display: 'block',
    // },
  },
  login:{
    color: 'black'
  },
  link:{
    textDecoration:'none',
    color:'black'
  }
}));

export default function SearchAppBar({Link}) {
  const [anchorEl, setAnchorEl] = React.useState(null);
  const [circle, setCircle] = React.useState(null);
  const handleClick = (event) => {
    setAnchorEl(event.currentTarget);
  };

  const handleClose = () => {
    setAnchorEl(null);
  };

  const handleMenu = (event) => {
    setCircle(event.currentTarget);
  };

  const handleCloseCircle = () => {
    setCircle(null);
  };
  
  const classes = useStyles();

  return (
    <div className={classes.root}>
      <AppBar position="static">
        <Toolbar>
            <IconButton
                edge="start"
                className={classes.menuButton}
                color="inherit"
                aria-label="open drawer"
                onClick={handleClick}
            >
                <MenuIcon />
            </IconButton>
            <Menu
                id="simple-menu"
                anchorEl={anchorEl}
                keepMounted
                open={Boolean(anchorEl)}
                onClose={handleClose}
            >
                <MenuItem onClick={handleClose}><Link className={classes.link} to="/patitasCatan/">Inicio</Link></MenuItem>
                <MenuItem onClick={handleClose}><Link className={classes.link} to="/patitasCatan/veterinarias">Veterinarias</Link></MenuItem>
                <MenuItem onClick={handleClose}><Link className={classes.link} to="/patitasCatan/lista">Lista</Link></MenuItem>
            </Menu>
            <Typography className={classes.title} variant="h6" noWrap>
                <PetsIcon fontSize="large"/>
                Patitas Catan
            </Typography>
              <IconButton
                aria-label="account of current user"
                onClick={handleMenu}
                color="inherit"
              >
                <AccountCircle />
              </IconButton>
              <Menu
                id="menu-appbar"
                anchorEl={circle}
                keepMounted
                open={Boolean(circle)}
                onClose={handleCloseCircle}
              >
                <MenuItem onClick={handleCloseCircle}><Link className={classes.link} to="/patitasCatan/login">Login</Link></MenuItem>
                <MenuItem onClick={() => authentication.signOut()}>
                  <Link className={classes.link} to="/patitasCatan/login">SingOut</Link>
                </MenuItem>
              </Menu>
          </Toolbar>
      </AppBar>
    </div>
  );
}
