import React from 'react';
import Grid from '@material-ui/core/Grid';
import { makeStyles } from '@material-ui/core/styles';

const useStyles = makeStyles(() => ({
  list: {
    display:'flex',
    flexFlow:'column',
    justifyContent: 'center',
    alignItems:'center',
    margin:'0.5em',
    height:'100%'
    }
}));

const Inicio = () => {
    const classes = useStyles();
    return ( 
        <>
            <Grid className={classes.list} container>
                <h1>Bienvenidos !!</h1>
                <h3>Te ayudamos a encontrar la mejor veterinaria para tu mascota</h3>
                <p>
                    En Patitas Catan encontraras la informacion mas actualizada sobre las veterinarias que se encuentren en la ciudad de Gonzalez Catan. Podras dejar tus comentarios y votar positivamente o negativamente, esto ayudara a los distintos usuarios a elegir el mejor centro de atencion para sus mascotas.     
                </p>
            </Grid>
        </>
     );
}
 
export default Inicio;