import React from 'react';
import AppBar from './components/AppBar';
import Footer from './components/Footer';
import Registro from './components/Registro';
import Login from './components/Login';
import Section from './components/Section';
import Lista from './components/Lista';
import Container from '@material-ui/core/Container';
import {
  BrowserRouter as Router,
  Switch,
  Route,
  Link
} from "react-router-dom";
import PrivateRoute from './components/PrivateRoute';
import {AuthProvider} from './components/Auth';
import Inicio from './components/Inicio';

function App() {
  return (
    <div className="container">
      <AuthProvider>
          <Router>
                <div className="app-container">
                    <AppBar Link={Link}/>
                    <Switch>
                        <Route exact path="/patitasCatan/">
                          <Inicio/>
                        </Route>
                        <PrivateRoute path="/patitasCatan/veterinarias" component={Section}/>
                        <PrivateRoute path="/patitasCatan/lista" component={Lista}/>
                        <Route path="/patitasCatan/login">
                          <Container maxWidth="sm">
                            <Login/>
                          </Container>
                        </Route>
                        <Route path="/patitasCatan/registro">
                          <Container maxWidth="sm">
                            <Registro/>
                          </Container>
                        </Route>
                    </Switch>
                </div>
            </Router>
        <Footer/>
      </AuthProvider>
    </div>
  );
}

export default App;
