import React from 'react';
import Grid from '@material-ui/core/Grid';
import { makeStyles } from '@material-ui/core/styles';


const useStyles = makeStyles(() => ({
  list: {
    display:'flex',
    flexFlow:'column',
    justifyContent: 'center',
    alignItems:'center'
}}));

const Lista = () => {
    const classes = useStyles();
    return ( 
            <Grid className={classes.list} container>
                <h1>Lista de Veterinarias</h1>
                <ul>
                    <li>Veterinaria Goberna</li>
                    <li>Veterinaria La Salle</li>
                    <li>Veterinaria Patitas</li>
                    <li>Hospital Veterinario Gonzalez Catán</li>
                    <li>Veterinaria El Gran Danes</li>
                    <li>Matymar's Veterinaria</li>
                    <li>Veterinaria Huelay</li>
                    <li>Veterinaria Maya</li>
                    <li>Veterinaria Yenu Aiken</li>
                    <li>Argos</li>
                    <li>Consultorio Veterinario Veneros</li>
                    <li>Veterinaria Y Pet Shop Athena</li>
                </ul>
            </Grid>
     );
}
 
export default Lista;