import React, {useState, useEffect, useMemo} from 'react';
import {db} from '../firebase';
import { makeStyles } from '@material-ui/core/styles';
import IconButton from '@material-ui/core/IconButton';
import { ThumbUpAlt, ThumbDownAlt } from '@material-ui/icons';

const useStyles = makeStyles((theme) => ({
  root: {
    '& > *': {
      margin: theme.spacing(1),
    },
  }
}));

export default function VotoButtons({veterinariaKey}) {
  const classes = useStyles();
  const [votoUsuario, setVotoUsuario] = useState({});
//   const [upVote, setUpVote] = useState(0);
//   const [downVote, setDownVote] = useState(0);
  
  const votoArriba = useMemo(() => {
      return votoUsuario.votoArriba;
  },[votoUsuario.votoArriba]);

  const votoAbajo = useMemo(() => {
      return votoUsuario.votoAbajo;
  },[votoUsuario.votoAbajo]);

  const findVoto = (data) => {
    const voto = data.find (voto => voto.id === veterinariaKey)
    const defaultValue = {
        idvoto: "",
        id: "",
        votoArriba: 0,
        votoAbajo: 0
    }
    if (!voto) {
        return defaultValue;   
    }
    return voto;
  }

  useEffect(()=>{
    
    return db.collection('votos')
    .onSnapshot((snapshot)=>{
      const data = [];
          snapshot.forEach((doc)=>{
            data.push({
                idvoto: doc.id,
                ...doc.data()});
          })
          setVotoUsuario(findVoto(data))
    })
  }, [findVoto])

  const handleClick = (type)=> () => {

    if (type === "upvote") {
        db.collection("votos").doc(votoUsuario.idvoto).set({
            id: votoUsuario.id,
            votoArriba: votoArriba + 1,
            votoAbajo: votoAbajo
        })
    } else {
        db.collection("votos").doc(votoUsuario.idvoto).set({
            id: votoUsuario.id,
            votoArriba: votoArriba,
            votoAbajo: votoAbajo + 1
        })
    }
    };

  return (
    <div className={classes.root}>
        <IconButton color="inherit" aria-label="upload picture" size="small">
          <ThumbUpAlt onClick={handleClick("upvote")}/>
          {votoArriba}
        </IconButton>
        <IconButton color="inherit" aria-label="upload picture" size="small">
          <ThumbDownAlt onClick={handleClick("downvote")}/>
          {votoAbajo}
        </IconButton>
    </div>
  );
}
